import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { UlsterPage } from '../pages/ulster/ulster';
import { MunsterPage } from '../pages/munster/munster';
import { ConnaughtPage } from '../pages/Connaught/Connaught';
import { LeinsterPage } from '../pages/Leinster/Leinster';
import { SkippingPage } from '../pages/Skipping/Skipping';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    UlsterPage,
    MunsterPage,
    ConnaughtPage,
    LeinsterPage,
    SkippingPage
    
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    UlsterPage,
    MunsterPage,
    ConnaughtPage,
    LeinsterPage,
    SkippingPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



