import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { UlsterPage } from '../../pages/ulster/ulster';
import { MunsterPage } from '../../pages/munster/munster';
import { ConnaughtPage } from '../../pages/Connaught/Connaught';
import { LeinsterPage } from '../../pages/Leinster/Leinster';
import { SkippingPage } from '../../pages/Skipping/Skipping';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController) {

  }
  openUlster() {
    this.navCtrl.push(UlsterPage);
    }
    openMunster() {
    this.navCtrl.push(MunsterPage)
    }
   openConnaught() {
    this.navCtrl.push(ConnaughtPage);
    }
    openLeinster(){
    this.navCtrl.push(LeinsterPage);
    }

    openSkipping(){
      this.navCtrl.push(SkippingPage);
      }
    




}



